import logo from './images/logo.png';
import './App.css';
import { Button, Nav, Navbar, CardColumns, Card, ListGroup, ListGroupItem } from 'react-bootstrap';
import ProductosJSON from './json/productos.json';
import { ScrollingProvider, useScrollSection, Section, useScrollSections } from 'react-scroll-section';

const DynamicMenu = () => {
  const sections = useScrollSections();
    return (
      <>
      <Navbar bg="light" expand="lg" sticky="top">
      <Navbar.Brand>
        <img
          src={logo}
          height="30"
          className="d-inline-block align-top"
          alt="Toshiro Sushi"
        />
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          {sections.map(({ id, onClick, selected }) => (
            <Nav.Link href={"#" + id} key={id} onClick={onClick} selected={selected}>{id}</Nav.Link>
          ))}
        </Nav>
      </Navbar.Collapse>
    </Navbar>
      </>
    );
};

const App = () => {
  function formatPrecio(num) {
    return '$' + num.toFixed(0).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.')
  }
  return (
    <ScrollingProvider>
      <div style={{ padding: 0, margin: 0, paddingBottom:30, backgroundColor:'black'}}>
        <DynamicMenu />
          <div style={{paddingLeft: 30, paddingRight:30}}>
            {ProductosJSON.map((cat, idx) =>
              <Section id={cat.cat_nombre}>
              <div key={idx} style={{marginTop:10}}>
                <hr />
                <h4 style={{padding:20, color:'white'}}>{cat.cat_nombre}</h4>
                {cat.cat_productos.map((prod, sdx) => 
                <div key={sdx} class="card mb-3" style={{ padding: 0, margin: 0 }}>
                  <CardColumns>
                  <Card>
                    <Card.Img variant="top" src={prod.prod_imagen} />
                    <Card.Body>
                      <Card.Title><b>{prod.prod_nombre}</b></Card.Title>
                      <Card.Text>
                        {prod.prod_descripcion}
                      </Card.Text>
                    </Card.Body>
                      {prod.prod_opciones.map((opc, odx) => 
                          <ListGroup className="list-group-flush" key={odx}>
                            <ListGroupItem>
                              <p style={{fontSize: 13.2, margin:0, padding:0}}><b>{opc.opc_nombre}</b> â‡’ {formatPrecio(opc.opc_precio)} pesos</p>
                            </ListGroupItem>
                          </ListGroup>
                      )}
                  </Card>
                  </CardColumns>
                </div>
                )}
              </div>
              </Section>
            )}
          </div>
      </div>
    </ScrollingProvider>
  );
}

export default App;
